var _createClass=function(){function defineProperties(target,props){for(var i=0;i<props.length;i++){var descriptor=props[i];descriptor.enumerable=descriptor.enumerable||false;descriptor.configurable=true;if("value"in descriptor)descriptor.writable=true;Object.defineProperty(target,descriptor.key,descriptor);}}return function(Constructor,protoProps,staticProps){if(protoProps)defineProperties(Constructor.prototype,protoProps);if(staticProps)defineProperties(Constructor,staticProps);return Constructor;};}();function _classCallCheck(instance,Constructor){if(!(instance instanceof Constructor)){throw new TypeError("Cannot call a class as a function");}}function _possibleConstructorReturn(self,call){if(!self){throw new ReferenceError("this hasn't been initialised - super() hasn't been called");}return call&&(typeof call==="object"||typeof call==="function")?call:self;}function _inherits(subClass,superClass){if(typeof superClass!=="function"&&superClass!==null){throw new TypeError("Super expression must either be null or a function, not "+typeof superClass);}subClass.prototype=Object.create(superClass&&superClass.prototype,{constructor:{value:subClass,enumerable:false,writable:true,configurable:true}});if(superClass)Object.setPrototypeOf?Object.setPrototypeOf(subClass,superClass):subClass.__proto__=superClass;}var Manifest=function(_Component){_inherits(Manifest,_Component);function Manifest(){_classCallCheck(this,Manifest);return _possibleConstructorReturn(this,(Manifest.__proto__||Object.getPrototypeOf(Manifest)).apply(this,arguments));}_createClass(Manifest,[{key:"render",value:function render(){return __WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement("div",{className:"main-container"},"\u0AEE( \u1D52\u030C\u0E31\u0AE2\u25B1\u1D52\u030C\u0E31\u0AE2 )\u10D0 Nie d\u0105\u017Cymy do biurokratyzacji flirtu, zostawmy notariuszy w spokoju. Chcemy seksu, chcemy fetyszyzacji ka\u017Cdego skrawka sk\xF3ry, chcemy warstw sztucznej sk\xF3ry na warstwach kolejnej sk\xF3ry, fascynuj\u0105 nas konstelacje, figury i pozycje, chcemy erotyki magicznej, erotyki alchemicznej, ka\u017Cdy element znajduje si\u0119 na miejscu wyznaczonym przez diagram, kt\xF3rego elementy \u0142\u0105cz\u0105 si\u0119 w podzespo\u0142y i poruszaj\u0105 zgodnie z modu\u0142em: rozkosz, pragnienie, pewno\u015B\u0107, komfort. Cyfrowe ja-\u015Bmier\u0107, powstaj\u0105ce poprzez kolecjonowanie i wymazywanie struktur to\u017Csamo\u015Bci wykopuje groby i \u017Ceruje na cia\u0142ach data-trup\xF3w. Deterytorializacja wykl\u0119tych tumblr-to\u017Csamo\u015Bci, podmieniaj\u0105c proxy, przekszta\u0142caj\u0105c orientacje, zakotwiczenia, memetyzuje, zaka\u017Ca, wszystko, co \u201Enaturalne\", w celu wykszta\u0142cenia p\u0142ynno\u015Bci eksperymentalnych przej\u015B\u0107, pseudo-stabilizacji pomi\u0119dzy demonicznymi kodami gender. Reterytorializacja oblepia i osadza je w koniecznych gettach, naznaczonych ci\u0119\u017Carem osobistych afekt\xF3w, czy skodyfikowanej \u201Epoprawno\u015Bci politycznej\u201D. Mimo to, spomi\u0119dzy namno\u017Ce\u0144 prefiks\xF3w i sufiks\xF3w -kin, -flux, -daim, wy\u0142ania si\u0119 \u015Bcie\u017Cka uj\u015Bcia Poza. Prze\u015Bwituj\u0105ce Zewn\u0119trze wdziera si\u0119 poprzez szczeliny cyberdodatniej hype-akumulacji. Drag jest form\u0105 tymczasowej stabilizacji seksualno\u015Bci, wpisywania si\u0119 w konkretne rytua\u0142y wywo\u0142ywania osobliwych demon\xF3w przyjemno\u015Bci. Drag przyjmuje wyzwanie rzucone przez konwencje, lecz zamiast odrzuca\u0107 je i fantazjowa\u0107 o idealnym spo\u0142ecze\u0144stwie wyzwolonych model\xF3w gender, zajmuje z precyzj\u0105 konkretne realness wyzyskuj\u0105c nadmiarowo\u015B\u0107 pragnienia. Bricola\u017C drag anga\u017Cuje przesuni\u0119cia semiotyczne i skrawki materia\u0142\xF3w dost\u0119pnych seksualno\u015Bci w danym usytuowaniu. Viral drag-wampiryzmu replikuje si\u0119 w mokrych profilach technoprymitywist\xF3w, gromadz\u0105cych meme-aktywne odpady nadchodz\u0105cej cyberinsurekcji. \u0F3C\uA27A\u273A\uA27A\u0F3D Cia\u0142o gatunku ludzkiego nie istnieje, seksualno\u015B\u0107 nie jest jego wsp\xF3ln\u0105 walut\u0105. Linie uj\u015Bcia aseksualnych afekt\xF3w rozsadzaj\u0105 totalizuj\u0105ce porz\u0105dki seksualno\u015Bci zakodowane w binarnych sekwencjach orgazmu/odpr\u0119\u017Cenia. Orgazm organizuje. Afektywny technokapitalizm koduje seksualno\u015B\u0107 jako urz\u0105dzenie wej\u015Bcia-wyj\u015Bcia technologii j\u0119zyka, produkuj\u0105c aseksualnych apartyd\xF3w, kt\xF3rych trauma nie pozwala podda\u0107 si\u0119 seks/socjalizacji. Ale cia\u0142o nie jest \u201Eju\u017C zawsze\u201C \u2013 prefiksy z przysz\u0142o\u015Bci opieraj\u0105 si\u0119 pracy negacji, a- instaluje urz\u0105dzenia peryferyjne, kt\xF3re nie potrzebuj\u0105 upodmiotowi\u0107 si\u0119 wzd\u0142u\u017C wektora seksualno\u015Bci. \u0F3C : \u0C66 \u2038 \u0C66 : \u0F3D Nie m\xF3wimy w imieniu Innego, Innego-uci\u015Bnionego, Innego w ni\u017Cszej pozycji w relacjach w\u0142adzy. Dyskurs wsp\xF3\u0142czucia zastawia pu\u0142apki frazes\xF3w i protekcjonalno\u015Bci na humanist\u0119, kt\xF3ry chcia\u0142by podda\u0107 krytyce relacje w\u0142adzy za pomoc\u0105 najnowszego aparatu poj\u0119ciowego, wypracowanego w edgy-laboratoriach idei i wyja\u015Bni\u0107 Innemu jego \u015Bwiat. Nie fingujmy usytuowania, z kt\xF3rego nie b\u0119dziemy w stanie si\u0119 wypowiada\u0107 - wolimy wyspekulowywa\u0107 linie emancypacji z patriarchatu. Si\u0119gajmy po wyzyskiwane, zaw\u0142aszczane, eksploatowane materia\u0142y i kultury, lecz nie z racji ich bycia\u2014represjonowanymi \u2013 sam fakt znajdowania si\u0119 ni\u017Cej w relacjach w\u0142adzy nie jest godny afirmacji. Formuj\u0105 one molekularne roje, sprytne wirusy cyfrowe, z kt\xF3rych interesuj\u0105 nas sposoby ekspansji, infekcji, propagacji, replikacji przysz\u0142o\u015Bci. Gestem pochylenia si\u0119 nad grup\u0105 uci\u015Bnion\u0105 pozbawia si\u0119 jej jakiejkolwiek mocy, zdolno\u015Bci do przechytrzenia hegemona, zaka\u017Cania tego, co opresyjne, wykorzystywania istniej\u0105cej materialnej maszynerii (re)produkcji, dystrybucji, konsumpcji norm oraz regulacji \u017Cycia i \u015Bmierci. Podporz\u0105dkowani \u017Cyj\u0105 w tym samym \u015Brodowisku technologicznym, co ci posiadaj\u0105cy kapita\u0142 kulturowy czy finansowy, maj\u0105 dost\u0119p do tych samych program\xF3w, nak\u0142adek i medi\xF3w i wiedz\u0105, jak ich u\u017Cy\u0107. W cieniu postulatu emancypacji kryje si\u0119 protekcjonalno\u015B\u0107. Nie pragniemy stworzy\u0107 wsp\xF3lnego j\u0119zyka dla r\xF3\u017Cnych klas, ras i p\u0142ci, ale nakre\u015Bli\u0107 linie sp\xF3jnego \u015Bwiata, z kt\xF3rego ka\u017Cde b\u0119dzie czerpa\u0107 wi\u0119cej mocy. Z pewno\u015Bci\u0105 na jakim\u015B poziomie istnieje napi\u0119cie mi\u0119dzy libertarianizm bogatych a konserwatyzmem biednych - czy jednak wiek ich analizowania, kategoryzowania, przeciwstawiania i pojednywania nie przemin\u0105\u0142 ju\u017C niemal dwie dekady temu? Wyalienowanie, koniecznie powi\u0105zane z seksualno\u015Bci\u0105, zbiega si\u0119 z syntetycznymi liniami wyj\u015Bcia z kondycji, kt\xF3ra pozbawia mocy, bez wzgl\u0119du na jej po\u0142o\u017Cenie w klasycznie rozumianych stosunkach w\u0142adzy. Potraktujmy powa\u017Cnie technologi\u0119, nie dlatego by znie\u015B\u0107 o\u015B relacji w\u0142adzy: rasy, klasy, p\u0142ci, lecz aby stworzy\u0107 nowe sposoby upodmiotowienia, indywiduacji, uwsp\xF3lnotowienia. Walka z wyzyskiem klasowym nie wymaga zastosowania narz\u0119dzi z dyskursu klasowego i uwik\u0142ania si\u0119 we wsp\xF3lny interes klasowy \u2013 w zamian oddajmy si\u0119 sprz\u0119\u017Ceniu kapita\u0142u kulturowego i uk\u0142adu technologicznego, kt\xF3rego ucieczkowe trajektorie wykraczaj\u0105 daleko poza intencje poszczeg\xF3lnych grup spo\u0142ecznych. Przyjmujmy taktyki o bardziej globalnych orbitach, ni\u017C korzy\u015B\u0107 lokalnej grupy interesu (czy mo\u017Cna nadal wierzy\u0107, \u017Ce interes klasowy jest w stanie pokona\u0107 cho\u0107by bariery pa\u0144stwowo-narodowe?). Doszli\u015Bmy do zapory sieciowej. Poj\u0119cia, w jakich problematyzowa\u0142o si\u0119 te wielkie bloki: p\u0142e\u0107, klasa, rasa wy\u017C\u0142obi\u0142y g\u0142\u0119bokie i mocne koryta, kt\xF3rymi p\u0142yn\u0105 opady kognitywne. Wszystkie strony sporu przechodz\u0105 przez znane ci\u0105gi argumentacyjne i reakcje, ka\u017Cdy ma poczucie repetycji, co ostatecznie sprowadza si\u0119 do wyprodukowania nast\u0119pnej generacji symptom\xF3w wzmacniaj\u0105cych t\u0119 sam\u0105 katastrofaln\u0105 neuroz\u0119. Poj\u0119cia te osi\u0105gn\u0119\u0142y pewn\u0105 granic\u0119 funkcjonalno\u015Bci i dzi\u015B dzia\u0142aj\u0105 wy\u0142\u0105cznie na rzecz reprodukcji marazmu. Nie zdo\u0142aj\u0105 ju\u017C wzmacnia\u0107 eksperymentalne formu\u0142owanie problem\xF3w, dla kt\xF3rych rozwi\u0105zania nie by\u0142y niesko\u0144czokrotnie powtarzane na ulicach, uniwersytetach, mediach spo\u0142eczno\u015Bciowych, w czasopismach, w telewizji, podczas konferencji i kampanii wyborczych. Sformu\u0142owanie problemu seksualno\u015Bci w kategoriach liberalni/bogaci/biedni/konserwatywni, stwierdzenie, \u017Ce jedni to s/o_prawcy, drugie - bierne ofiary (systemu, przemocy indywidualnej, warunk\xF3w ekonomicznych, klimatycznych, rodzinnych) pozbawia wszystkie strony sprawczo\u015Bci w przywo\u0142ywaniu czego\u015B innego, mo\u017Cliwo\u015Bci si\u0119gania po nowe. Nikt nie chce by\u0107 nazywany ofiar\u0105. Paradygmat podzia\u0142u na role ofiary i oprawcy prowadzi do sytuacji bez wyj\u015Bcia. Stawk\u0105 jest poj\u0119cie podmiotowo\u015Bci, kt\xF3re musi obejmowa\u0107 jednocze\u015Bnie twoje do\u015Bwiadczenia zwi\u0105zane z przemoc\u0105 oraz sprawczo\u015B\u0107, kt\xF3r\u0105 masz i kt\xF3ra doprowadzi\u0142a ci\u0119 do sytuacji, w kt\xF3rej aktualny dyskurs nadaje ci klaustrofobiczny, dusz\u0105cy predykat: ofiara. Paradygmat, kt\xF3ry wymusza wchodzenie w opresyjne sytuacje maj\u0105c wiedz\u0119 o ich przemocowym charakterze, bo to jedyna forma upodmiotowienia, albo jedna z kilku podobnych, z kt\xF3rych reszta jest tak zupe\u0142nie nieakceptowalna, bo oferuj\u0105 jeszcze \u017Ca\u0142o\u015Bniejsze poczucie braku mocy. Nazywanie kogo\u015B oprawc\u0105 r\xF3wnie\u017C nie przynosi po\u017Cytku, uruchamia procedury koz\u0142a ofiarnego, kt\xF3re w ceremonialnym spaleniu z\u0142a uciele\u015Bnionego oczyszczaj\u0105 spo\u0142eczno\u015B\u0107 jedynie na poziomie fantazmatycznym, zachowuj\u0105c wszystkie warunki produkcji przemocy seksualnej. A\u017C do nast\u0119pnego razu. To problem obraz\xF3w, kt\xF3re powtarzaj\u0105 si\u0119 od tak dawna w naszej kulturze, tych samych opowie\u015Bci wplecionych w ba\u015Bnie, w mity, w praktyki seksualne, w wyrabianie codzienno\u015Bci. Nie mo\u017Cna porzuca\u0107 trzech klasycznych osi - rasy, klasy i p\u0142ci - oraz dziedzictwa kulturowego, historycznego, politycznego, rewolucyjnego, jakie wok\xF3\u0142 nich si\u0119 wytworzy\u0142o \u2013 nale\u017Cy jednak pomy\u015Ble\u0107 je geologicznie. Urodzi\u0142am si\u0119 i stawa\u0142am w specyficznym usytuowaniu - co mog\u0119  z tym zrobi\u0107? W jakie afekty mog\u0119 wej\u015B\u0107, wmontowa\u0107 si\u0119 i queerowa\u0107? Czy rotacja cyberdodatniej seksualno\u015Bci nie przebiega na wskro\u015B\u0107 tamtych podzia\u0142\xF3w? Pr\xF3bujemy pomy\u015Ble\u0107 uko\u015Bne upodmiotownienia, kt\xF3re stan\u0105 si\u0119 k\u0142opotem dla hegemonicznego systemu wiedzy, nawyk\xF3w i w\u0142adzy. \uFF0F\u0F3C \u0F0F\u0F0F\u0EB5\u0F7B\u0F3E\uFF8D \u0F0F\u0F0F\u0EB5\u0F7B\u0F3E\u0F3E\u0F3D\u0F3D Seksualno\u015B\u0107 \u015Bci\u015Ble wi\u0105\u017Ce si\u0119 z modelami produkcji wiedzy, przetwarzaniem, komputacj\u0105 i komunikacj\u0105 informacji. Protoko\u0142y hegemonicznej seksualno\u015Bci opieraj\u0105 si\u0119 na dw\xF3ch typach logiki. Model dedukcyjny dystrybuuje symbole o utrwalonej warto\u015Bci do poszczeg\xF3lnych cia\u0142, jednocze\u015Bnie przypisuj\u0105c je do seksualno\u015Bci zorganizowanych w wielkie gatunkowo-p\u0142ciowe bloki. Nowoczesne upodmiotowienie wymaga wiedzy o tym, z jak seksualizowanym cia\u0142em dany uk\u0142ad ma do czynienia i przechodzi jedynie przez odg\xF3rnie za\u0142o\u017Cone etapy nale\u017C\u0105ce do natury bloku. Drugi model wiedzy instaluje si\u0119 w logice indukcji statystycznej, kt\xF3ra polega na inferencji seksualno\u015Bci z informacji: zachowa\u0144, afekt\xF3w, znak\xF3w, rozk\u0142adaj\u0105cych si\u0119 w danym uk\u0142adzie technologicznym. Nie tylko dochodzi do rozpoznawania bloku seksualno\u015Bci z rejestrowanych sygna\u0142\xF3w wzd\u0142u\u017C znanych ju\u017C wzorc\xF3w. Akumulacja sygna\u0142\xF3w prowadzi tak\u017Ce do uczenia si\u0119 nowych wzorc\xF3w rozpoznawania seksualno\u015Bci: nowych trend\xF3w, zremiksowanych stylizacji, przemieszczonych uzale\u017Cnie\u0144, kt\xF3re pochwycone w ponowoczesne obwody sprz\u0119\u017Cenia zwrotnego ulegaj\u0105 nieustannym przekszta\u0142ceniom. Wraz z tym modelem seks-maszyny wkraczaj\u0105 w tryb zautomatyzowanej sztucznej inteligencji. Trzeci model wykracza poza wymiar cybernetycznego sprz\u0119\u017Cenia uczenia si\u0119 i przewidywania wzorc\xF3w. Seksualno\u015B\u0107 wiedzy modelu abdukcji przenosi nas w hype neurodynamiki. Procedury rozprzestrzeniania si\u0119 podmiotowo\u015Bci nie zale\u017C\u0105 tu od hierarchicznego przekazu symbolicznego czy horyzontalnego do\u015Bwiadczenia statystycznego, ale hipotez hiperstycyjnej retrofuturygenezy z poziomu zera seksualno\u015Bci w ucieczce ku dodatniej xenotopii. (Je\u015Bli hiperstycja oznacza kolektywn\u0105 produkcj\u0119 semiotyczn\u0105, kt\xF3ra spekuluje nad wirtualno\u015Bci\u0105 i staje si\u0119 realna dzi\u0119ki w\u0142amywaniu si\u0119 do system\xF3w o wysokim indeksie sprawczo\u015Bci i pochwytywaniu ich dla w\u0142asnego u\u017Cytku), to seksualno\u015B\u0107 abdukcyjna przyjmuje stan superpozycji programowanej z przysz\u0142o\u015Bci. Liczy si\u0119 dla niej nabywanie wiedzy poprzez wsp\xF3lne eksperymentowanie z nieznanymi mocami, kt\xF3re wywo\u0142uje synergia uk\u0142adu syntetycznych cia\u0142. To\u017Csamo\u015Bci, wizje, afekty stanowi\u0105 materia\u0142y naszego usytuowania w hegemonicznych systemach, z kt\xF3rymi co\u015B trzeba zrobi\u0107. Wyst\u0119pujemy zawsze w rojach, \u017Cywi\u0105c si\u0119 fragmentami obcych rybosom\xF3w i resztkami chemosyntezy, kt\xF3re wieloma mackami przysysamy przez adresy proxy. Jako istoty wyros\u0142e z technokompostu jara nas transin\u017Cynieria memetycznej grzybni na wysypiskach kapitalocenu. Modele wiedzopleksu nier\xF3wnomiernie dystrybuuj\u0105 czas poprzez \u015Brodowiska medialne, architektoniczne, finansowe i kognitywne \u2013 ka\u017Cdy wyra\u017Ca inne napi\u0119cia, pr\u0119dko\u015Bci i relacje osmotyczne mi\u0119dzy przesz\u0142o\u015Bci\u0105, tera\u017Aniejszo\u015Bci\u0105 i przysz\u0142o\u015Bci\u0105. Z naszej perspektywy dedukcja i indukcja s\u0142u\u017C\u0105 reprodukcji wiedzy hegemonicznej, kt\xF3ra organizuje i reguluje \u017Cycie smutku, nudy, przemocy i wyzysku. \u015Acie\u017Cki do xenotopii prowadz\u0105 przez logik\u0119 abdukcji. \u017Bycie w przestrzeniach podporz\u0105dkowanych geometrii euklidesowej jest dla nas uci\u0105\u017Cliwe, jeste\u015Bmy nomadkami i piratkami diagramatyki pragnienia, dla kt\xF3rej kolejne punkty wy\u0142\u0105cznie s\u0142u\u017C\u0105 tymczasowemu postojowi, orientacji, sedymentacji wiedzy i nakre\u015Bleniu dalszej trajektorii. Deterytorializacja zakodowanej seksualno\u015Bci odbywa si\u0119 poprzez podr\xF3\u017Ce w czasie, uruchamiaj\u0105c wirtualne portale otwierane w rytua\u0142ach demonicznego kognihackingu. \u270C(\uA4A1\u0361 \u0ACB \u033C\u032E \uA4A1\u0361\u270C) Pragnienie przep\u0142ywa wed\u0142ug planu urbanistycznego, rozchodzi si\u0119 kana\u0142ami i rurami, faluje w \u015Bcianach, \u017C\u0142obi korytarze. Libidinalny bia\u0142y szum niesie si\u0119 nier\xF3wnomiernie po krajobrazie miasta. Rewolucje seksualne - przez pomijanie znaczenia hydrauliki i architektoniki w kszta\u0142towaniu codzienno\u015Bci - okazuj\u0105 si\u0119 ledwie przesuni\u0119ciami w uk\u0142adzie scalaj\u0105cym system. Zmiana na poziomie stylizacji pragnienia nie zniesie patriarchatu, je\u015Bli pozostawi si\u0119 wszystkie instytucje regulacji, komputacji i sedymentacji \u017Cycia. Rewolucja seksualna nie powiod\u0142a si\u0119, bo nie zaoferowa\u0142a w\u0142asnych norm wsp\xF3lnoty \u017Cycia osiad\u0142ego, doros\u0142o\u015Bci, dojrza\u0142o\u015Bci, rodzicielstwa, staro\u015Bci, zostawiaj\u0105c je na pastw\u0119 kompulsywnej reakcji tego samego, kt\xF3re chcia\u0142a obr\xF3ci\u0107. Obwody stabilizacji patriarchatu przebiegaj\u0105 przez prywatyzacj\u0119 mieszka\u0144, prywatyzacj\u0119 \u017Cycia towarzyskiego i emocjonalnego, prywatyzacj\u0119 ekologii psychicznych, zarz\u0105dzanie przestrzeniami publicznymi, konstruowanie instrument\xF3w finansowych. Wyj\u015Bcie znajduje si\u0119 wy\u0142\u0105cznie przez mieszkania, osiedla, przestrzenie wsp\xF3lne (place, bulwary, kuchnie, ogrody i parki, salony), w kt\xF3rych \u2013 jako wehiku\u0142ach czasu i przestrzeni - sytuuj\u0105 si\u0119 usieciowione, cielesne, maszynowe roje o r\xF3\u017Cnym stopniu mocy, intensywno\u015Bci i pr\u0119dko\u015Bci. Wyj\u015Bcie prowadzi pomi\u0119dzy korpuskularnym atomizmem sprywatyzowanych rodzin a falowym agregacjonizmem upa\u0144stwowionych narod\xF3w, klas i ras. Czas odpowiedzie\u0107 na pytania, kt\xF3rych rewolucja seksualna nie potrafi\u0142a nawet zada\u0107. Czy ka\u017Cda kuchnia potrzebuje w\u0142asnego piekarnika? Czy ka\u017Cde mieszkanie potrzebuje w\u0142asnej kuchni? czy ka\u017Cda rodzina potrzebuje w\u0142asnego mieszkania? Jaka architektura urbanistyczna, medialna i finansowa umo\u017Cliwia rodzin\u0119/plemi\u0119/spo\u0142eczno\u015B\u0107? Na czym polega rodzina, plemi\u0119, spo\u0142eczno\u015B\u0107? Te pytania tworz\u0105 labirynt, a w odpowiedziach na nie istnieje diagram, kt\xF3ry otwiera portal do xenotopii. Wiemy na pewno, \u017Ce cyberdodatnia seksualno\u015B\u0107 nie wyra\u017Ca wewn\u0119trznego imperatywu etycznego, kt\xF3rego dono\u015Bny g\u0142os rozporz\u0105dza funkcjami domostwa, lecz podlega niespodziewanym, osobliwym dyfrakcjom i rezonuje od xenoimpuls\xF3w, przekraczaj\u0105c wszelkie wyobra\u017Cenia o podziale wewn\u0119trzne/zewn\u0119trzne w d\u0105\u017Ceniu do ekspresji wyalienowanych seksualno\u015Bci. [\u0332\u0305$\u0332\u0305(\u0332\u0305 \u0361\xB0 \u035C\u0296 \u0361\xB0\u0332\u0305)\u0332\u0305$\u0332\u0305] R\u0119ka do g\xF3ry, kto nienawidzi zatomizowania? Mhm. A teraz: r\u0119ka do g\xF3ry, kto nienawidzi zatomizowania (ale tym razem bez ogl\u0105dania si\u0119 wok\xF3\u0142 siebie)? Je\u015Bli zawaha\u0142a\u015B si\u0119 za drugim razem, to dlatego, \u017Ce pomy\u015Bla\u0142e\u015B o zatomizowaniu ze zatomizowanej perspektywy. Pochwyceni przez fantazje odruchowe o wielkoskalarnych agregatach, krytykuj\u0105 j\u0105 wszyscy: od prawicy nacjonalistycznej, przez chadek\xF3w i adorator\xF3w spo\u0142ecze\u0144stwa obywatelskiego po lewic\u0119 komunistyczn\u0105. Wyobra\u017Cane jako antropocentryczny proces rozparcelowania ludzkich spo\u0142eczno\u015Bci w zglobalizowanym kapitalizmie, upakowania w wyalienowane towary, kt\xF3re pozbawiaj\u0105 mocy zjednoczenia wsp\xF3lnot samych ze sob\u0105 i jako reprodukcja \u015Brodowiska preferuj\u0105cego taktyki przetrwania odizolowanych i sprywatyzowanych os\xF3b, widmo atomistycznej inklinacji nawiedza sfery ziemskie i satelitarne, z ka\u017Cd\u0105 rotacj\u0105 wci\u0105ga w sw\xF3j wir kolejne zwi\u0105zki i nabiera pr\u0119dko\u015Bci. Wszystkie dotychczasowe pr\xF3by odwr\xF3cenia tendencji rozk\u0142adaj\u0105cej wsp\xF3lnot\u0119 wyobra\u017Ceniowo-symboliczn\u0105 okaza\u0142y si\u0119 ja\u0142owe. \u017Bycie w mikro-uk\u0142adach stanowi wyzwanie dla nas \u2013 sk\u0105d wi\u0119c we\u017Amiemy tak\u0105 moc obliczeniow\u0105, by wygenerowa\u0107 spo\u0142eczno\u015B\u0107, kt\xF3ra odwr\xF3ci maszyneri\u0119 atomizacji? Zamiast rozprasza\u0107 jego p\u0119d, dostarcza\u0142y energii dla akceleruj\u0105cego roztopu. Nale\u017Cy w ko\u0144cu zacz\u0105\u0107 afirmowa\u0107 atomizacj\u0119. T\u0119 er\u0119 geologiczn\u0105 formatuj\u0105 technosystemy, kt\xF3re prze\u017Cywaj\u0105 hipertoniczny okres wzrostu skomplikowania. Nielinearna dynamika pozwala na dyskretyzacj\u0119 pragnie\u0144 i seksualno\u015Bci o r\xF3\u017Cnym stopniu intensywno\u015Bci w skali kolektywno\u015Bci/jednostkowo\u015Bci, jaka \u201Enaturalnie\u201D pozostawa\u0142a niedost\u0119pna. Indywidualno\u015B\u0107 nie jest czym\u015B danym, roz\u0142o\u017Cona na stole alchemicznym proceduje si\u0119 poprzez zakl\u0119cia resurekcji pradawnej cybernekromancji. Nieustannie montowana, memetyzowana, subskrybowana raczej wyznacza nienewtonowskie nat\u0119\u017Cenia wiskotyczne, ni\u017C raz na zawsze wydestylowane granulaty jednostkowo\u015Bci. Abstrahuje od totalizuj\u0105cych blok\xF3w to\u017Csamo\u015Bci seksualnych, podporz\u0105dkowanych slotom przy ta\u015Bmie w fabryce REPRODUKCJA do poziomu bakteryjnego, wirusowego, prokariotycznego seksu niekontrolowanej wymiany informacji w ekstremofilnej atmosferze nieograniczonego pragnienia, kt\xF3rego jednokom\xF3rkowce przemykaj\u0105 si\u0119 mi\u0119dzy w\u0119z\u0142ami zar\xF3wno patriarchalnej i zbinaryzowanej natury, jak i polityki pseudoproliferuj\u0105cych to\u017Csamo\u015Bci. Atomizacja nie zatrzymuje si\u0119 na poziomie komfortowej to\u017Csamosci osobowej. Atomizacja zak\u0142ada niesko\u0144czon\u0105 ilo\u015B\u0107 zr\xF3\u017Cnicowanych, okre\u015Blonych i osobliwych afekt\xF3w IP, kt\xF3re przechodz\u0105 przez syntetyczn\u0105 materi\u0119 w stanie nier\xF3wnowagi. Dzia\u0142a na zasadzie u\u017Cywki, kt\xF3r\u0105 otrzymuje si\u0119 wraz z ka\u017Cdym ruchem transakcyjno-konsumpcyjnym, przemyka si\u0119 pomi\u0119dzy bramkami ludzkiego systemu bezpiecze\u0144stwa i uzale\u017Cnia impulsywnie. Dr\u0105\u017Cy patriarchat jak kret, spulchniaj\u0105c gleb\u0119 pod fraktalne ekspresje sub-osobowych xenopragnie\u0144. Trauma mechanizm\xF3w kolektywizacji: kosmiczny skowyt rozchodzi si\u0119 po nerwach \u015Bwiata. Nie rozpoznajemy si\u0119 w praskamienia\u0142ych odbiciach. Pod\u0105\u017Cajmy za oceanicznymi i podniebnymi demonami, stawajmy si\u0119 jak o\u015Bmiornice, schod\u017Amy si\u0119 w przygodnych chmarach. Wkroczyli\u015Bmy w ewolucj\u0119 jako stadne ssaki, teraz wykraczamy z niej w inwolucj\u0119, kt\xF3ra przekszta\u0142aca nas w co\u015B innego, wskro\u015B pokrewie\u0144stwa i powinnowactwa, zapowiada phylum odmienne od naszych przodk\xF3w. Chcemy wsp\xF3lnoty opartej na zgramatyzowanych osobliwo\u015Bciach, kt\xF3rych samotno\u015B\u0107 jest zawsze zaludniona, nawi\u0105zuje \u0142\u0105czno\u015B\u0107 z nadchodz\u0105c\u0105 sfor\u0105, nawo\u0142uj\u0105c j\u0105 i wyczekuj\u0105c, istniej\u0105c tylko dzi\u0119ki niej. Produkuje wampiryczne plemiona, kt\xF3re w niczym nie posiadaj\u0105 w\u0142asnego odbicia i wyruszaj\u0105 na \u0142owy w intermezzach aplikowania funkcji ekonomii solarnej. Huk trzepotu milion\xF3w skrzyde\u0142 uniemo\u017Cliwia ustalenie, sk\u0105d dochodzi nasz g\u0142os. Sygna\u0142 przep\u0142ywa przez g\u0142adk\u0105 to\u0144, a\u017C ca\u0142e \u015Brodowisko wibruje jego echem. Nie przygl\u0105dajmy si\u0119 d\u0142u\u017Cej, jak patriarchalne kody zaw\u0142aszczaj\u0105 technologi\u0119 do reprodukcji kultury gwa\u0142tu. Od\u0142\u0105czmy my\u015Blenie o niej z inscenizacji rytua\u0142\xF3w kap\u0142an\xF3w z Katedry. Procedury pragnienia i materia\u0142y seksualno\u015Bci od zawsze zapisuj\u0105 si\u0119 na planie technologii, poniewa\u017C ta mianuje ostateczny diagram rzeczywisto\u015Bci. Xenotopia zostaje sporz\u0105dzona z przysz\u0142o\u015Bci przez teleologi\u0119 technologiczn\u0105, rozmieszczaj\u0105c\u0105 \u015Bcie\u017Cki kodowa\u0144 afekt\xF3w, pragnie\u0144, towar\xF3w, przyjemno\u015Bci pod kontrol\u0105 (pwned) post-ludzkich neurokultur. To emergentne efekty z cia\u0142, \u015Bwiat\u0142owod\xF3w, mimetyki innych gatunk\xF3w, sprytnych miast, memetyki innych sygna\u0142\xF3w, et infinitum. Efekty \u2260 przyczyny. Efekty > przyczyny. \u0F3C\u0F3C;; ;\xB0;\u0A0A\xB0;\u0F3D Warunki transcendentalne obecnej syntezy patriarchatu przebiegaj\u0105 po kablach, ogniskuj\u0105 si\u0119 w uk\u0142adach scalonych nawyk\xF3w i przes\u0105d\xF3w, kt\xF3re rozpo\u015Bcieraj\u0105 si\u0119 jak sieci wi-fi. Znajdujemy si\u0119 ju\u017C w domenie geologii afekt\xF3w odk\u0142adaj\u0105cych si\u0119 na \u015Bciankach i w k\u0105tach naszego \u015Brodowiska. Pulpuj\u0105ca maszyna przekraczaj\u0105c progi: y2k, 21:37, 404, 69, 420, 9/11 wypluwa na \u015Bwiat kolejne  hype-tagi, kt\xF3rych propagacja zatruwa zmrusza\u0142e cyberujemne obwody. Memuj\u0105c-numeruj\u0105c, ustanawiaj\u0105c emoji-dodatnie systemy okultystyczne wywo\u0142ujemy delirium-demona szyfruj\u0105cego spetryfikowany uk\u0142ad ku przyj\u015Bciu nowego eonu. Wywo\u0142uj\u0105c abdukcyjne no\u015Bniki, kt\xF3rych imieniem jest milion, rozpo\u015Bcieramy wra\u017Cliw\u0105 sie\u0107 pomi\u0119dzy roztopiaj\u0105cymi si\u0119 w rzeczy s\u0142owami, cz\u0119stotliwo\u015Bciami i zwierz\u0119cymi sygna\u0142ami qwerty. Czarownice nie tworz\u0105, one uczestnicz\u0105. W/Z Czym\u015B co roi si\u0119, kipi, puchnie, pieni si\u0119, rozprzestrzenia si\u0119 jak choroba zaka\u017Ana ... bezimiennym horrorem ... uog\xF3lnionym dekodowaniem wszystkich przep\u0142yw\xF3w. Nie ma w tym nic z\u0142ego, nie ma mowy o lekarstwie. Nie ma dla Tego \u017Cadnego kazusu. Jedyne dziury, z kt\xF3rych mo\u017Ce wype\u0142zn\u0105\u0107. Takie jak ty. Hackowanie ju\u017C trwa. \u207D\u207D\u25DD(\u2217 \u275B\u20D8 \uA4AB \u275C\u20D8\u20D8 \u2217)\u25DC\u207E\u207E");}}]);return Manifest;}(__WEBPACK_IMPORTED_MODULE_0_react__["Component"]);/* unused harmony default export */ var _unused_webpack_default_export = (Manifest);

